@font-face {
    font-family: "CiscoSansTT";
    src: url('./static/CiscoSansTTLight.woff2') format('woff2');
    font-weight: 100;
}
@font-face {
    font-family: "CiscoSansTT";
    src: url('./static/CiscoSansTTBold.woff2') format('woff2');
    font-weight: bold;
}
@font-face {
    font-family: "CiscoSansTT";
    src: url('./static/CiscoSansTTCondRegular.woff2') format('woff2');
    font-stretch: condensed;
}
@font-face {
    font-family: "CiscoSansTT";
    src: url('./static/CiscoSansTTRegular.woff2') format('woff2');
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote,
pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s,
samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article,
aside, canvas, details, embed,  figure, figcaption, footer, header, hgroup,  menu, nav, output,
ruby, section, summary, time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    font-size: 100%;
    vertical-align: baseline;
}

article, aside, details, figcaption, figure,  footer, header, hgroup, menu, nav, section {
    display: block;
}

body {
    line-height: 1;
}


ol, ul {
    list-style: none;
}

blockquote, q {
    quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

input::placeholder {
    /* Medium gray. This is the only way to change the color of placeholder. */
    color: #B6B9BB;
}
